
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, FooterBtnEvent, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { ObsValue } from '@/services/observation_service'
import { HtsArtReferralOutcomeService } from "@/apps/HTS/services/hts_art_referral_outcome_service"
import { getFacilities } from '@/utils/HisFormHelpers/LocationFieldOptions'
import { generateDateFields } from '@/utils/HisFormHelpers/MultiFieldDateHelper'

export default defineComponent({
    components: { IonPage },
    mixins: [EncounterMixinVue],
    data: () => ({
      service: {} as HtsArtReferralOutcomeService
    }),
    watch: {
      ready: {
        handler(ready: boolean) {
          if (ready) {
            this.service = new HtsArtReferralOutcomeService(this.patientID, this.providerID)
            this.fields.push(this.artReferralOutcomeField())
            this.fields = this.fields.concat(this.artOutcomeDateFields())
            this.fields.push(this.artSiteField())
            this.fields.push(this.arvNumberField())
          }
        },
        immediate: true
      }
    },
    methods: {
      async onFinish(_: any, computedData: any) {
        const obs = await this.resolveObs(computedData)
        await this.service.createEncounter()
        await this.service.saveObservationList(obs as ObsValue[])
        this.nextTask()
      },
      artReferralOutcomeField(): Field {
        return {
          id: "outcome",
          helpText: 'ART Outcome Referral',
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          finalComputedValue: (v: Option) => {
            return {
              obs: this.service.buildValueCoded('ART referral', v.value as string) 
            }
          },
          options: () => {
            return this.mapStrToOptions([
                'Link',
                'Refused',
                'Died',
                'On ART',
                'Unknown'
            ])  
          }
        }
      },
      artSiteField(): Field {
        return {
          id: 'art_site',
          helpText: 'Visited Clinic Name',
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => f.outcome.value === 'On ART',
          finalComputedValue: (v: Option) => ({
            obs: this.service.buildValueText('Location of ART initialization', v.label)
          }),
          options: (_: any, filter='') => getFacilities(filter),
          config: {
            footerBtns: [
              {
                name: "Here",
                slot: "end",
                onClickComponentEvents: {
                  setValue: (btnEvent: FooterBtnEvent) => {
                    return btnEvent.btnOutput
                  }
                },
                onClick: () => HtsArtReferralOutcomeService.getLocationName()
              }
            ],
            showKeyboard: true,
            isFilterDataViaApi: true
          }
        }
      },
      artOutcomeDateFields(): Field[] {
        return generateDateFields({
          id: 'outcome_date',
          helpText: 'Outcome',
          required: true,
          minDate: () => this.patient.getBirthdate(),
          maxDate: () => this.service.date,
          condition: (f: any) => f.outcome.value === 'On ART',
          computeValue: (date: string) => {
            return {
                date,
                obs: this.service.buildValueDate('Date ART started', date)
            }
          },
          estimation: {
            allowUnknown: false
          }
        })
      },
      arvNumberField(): Field {
        return {
          id: 'arv_number',
          helpText: 'ARV Number',
          type: FieldType.TT_TEXT,
          isRequired: () => true,
          condition: (f: any) => f.outcome.value === 'On ART',
          finalComputedValue: (v: Option) => {
            return {
                obs: this.service.buildValueText('Art number', `${v.value}`)
            }
          }
        }
      }
    }
})
